"use client";

import { motion } from "framer-motion";
import { SectionTitle, Card, Button } from "@/components/ui";
import { 
  Globe, 
  Smartphone, 
  Palette, 
  Code2, 
  ShoppingCart, 
  Wrench,
  ArrowRight,
  Check,
  Search,
  PenTool,
  Rocket,
  Headphones
} from "lucide-react";
import Link from "next/link";

const services = [
  {
    id: "web",
    icon: Globe,
    title: "Website Development",
    description: "Website modern, responsif, dan SEO-friendly untuk berbagai kebutuhan bisnis Anda.",
    features: [
      "Landing Page & Company Profile",
      "Custom Web Application",
      "Progressive Web App (PWA)",
      "CMS Integration",
      "Performance Optimization",
    ],
  },
  {
    id: "mobile",
    icon: Smartphone,
    title: "Mobile App Development",
    description: "Aplikasi mobile native dan cross-platform dengan performa tinggi.",
    features: [
      "iOS & Android Native",
      "React Native / Flutter",
      "Push Notifications",
      "Offline Support",
      "App Store Deployment",
    ],
  },
  {
    id: "design",
    icon: Palette,
    title: "UI/UX Design",
    description: "Desain antarmuka yang menarik, intuitif, dan user-centered.",
    features: [
      "User Research",
      "Wireframing & Prototyping",
      "Visual Design",
      "Usability Testing",
      "Design System",
    ],
  },
  {
    id: "custom",
    icon: Code2,
    title: "Custom Software",
    description: "Solusi software custom yang disesuaikan dengan kebutuhan spesifik bisnis.",
    features: [
      "ERP & CRM Systems",
      "POS Systems",
      "Inventory Management",
      "Business Intelligence",
      "API Development",
    ],
  },
  {
    id: "ecommerce",
    icon: ShoppingCart,
    title: "E-Commerce Solutions",
    description: "Toko online lengkap dengan fitur pembayaran dan manajemen yang mudah.",
    features: [
      "Product Catalog",
      "Shopping Cart & Checkout",
      "Payment Gateway Integration",
      "Inventory Management",
      "Order Tracking",
    ],
  },
  {
    id: "maintenance",
    icon: Wrench,
    title: "Maintenance & Support",
    description: "Layanan pemeliharaan dan support untuk menjaga aplikasi tetap optimal.",
    features: [
      "24/7 Monitoring",
      "Bug Fixing",
      "Security Updates",
      "Performance Optimization",
      "Feature Enhancements",
    ],
  },
];

const process = [
  {
    icon: Search,
    title: "Discovery",
    description: "Memahami kebutuhan, goals, dan tantangan bisnis Anda.",
  },
  {
    icon: PenTool,
    title: "Design",
    description: "Merancang solusi visual dan arsitektur sistem yang optimal.",
  },
  {
    icon: Code2,
    title: "Development",
    description: "Membangun produk dengan teknologi terbaik dan best practices.",
  },
  {
    icon: Rocket,
    title: "Launch",
    description: "Deployment dan launch produk ke production environment.",
  },
  {
    icon: Headphones,
    title: "Support",
    description: "Maintenance berkelanjutan dan support pasca-launch.",
  },
];

const pricing = [
  {
    name: "Basic",
    price: "5 Juta",
    description: "Untuk bisnis kecil yang baru memulai kehadiran online.",
    features: [
      "Landing Page (1-5 halaman)",
      "Responsive Design",
      "Basic SEO",
      "Contact Form",
      "1 Bulan Support",
    ],
    popular: false,
  },
  {
    name: "Professional",
    price: "15 Juta",
    description: "Untuk bisnis yang membutuhkan website lebih kompleks.",
    features: [
      "Website hingga 15 halaman",
      "CMS Integration",
      "Advanced SEO",
      "Custom Features",
      "3 Bulan Support",
      "SSL Certificate",
    ],
    popular: true,
  },
  {
    name: "Enterprise",
    price: "Custom",
    description: "Untuk kebutuhan enterprise dengan skala besar.",
    features: [
      "Unlimited Pages",
      "Custom Web Application",
      "High Availability",
      "Priority Support 24/7",
      "6+ Bulan Support",
      "Dedicated Team",
    ],
    popular: false,
  },
];

export default function ServicesPage() {
  return (
    <>
      {/* Hero */}
      <section className="py-24 gradient-hero">
        <div className="container mx-auto px-4 lg:px-8">
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5 }}
            className="max-w-3xl mx-auto text-center"
          >
            <span className="inline-block px-4 py-1.5 text-xs font-semibold text-primary bg-primary-50 rounded-full mb-4">
              Layanan Kami
            </span>
            <h1 className="text-4xl md:text-5xl font-bold text-foreground mb-6">
              Solusi Digital Lengkap untuk Bisnis Anda
            </h1>
            <p className="text-lg text-muted-foreground leading-relaxed">
              Dari website hingga aplikasi mobile, kami menyediakan layanan 
              pengembangan software end-to-end yang berkualitas.
            </p>
          </motion.div>
        </div>
      </section>

      {/* Services */}
      <section className="py-24">
        <div className="container mx-auto px-4 lg:px-8">
          <div className="space-y-24">
            {services.map((service, index) => (
              <motion.div
                key={service.id}
                id={service.id}
                initial={{ opacity: 0, y: 20 }}
                whileInView={{ opacity: 1, y: 0 }}
                viewport={{ once: true }}
                transition={{ duration: 0.5 }}
                className={`grid lg:grid-cols-2 gap-12 items-center ${
                  index % 2 === 1 ? "lg:flex-row-reverse" : ""
                }`}
              >
                <div className={index % 2 === 1 ? "lg:order-2" : ""}>
                  <div className="w-16 h-16 rounded-2xl bg-primary-50 flex items-center justify-center mb-6">
                    <service.icon className="w-8 h-8 text-primary" />
                  </div>
                  <h2 className="text-3xl font-bold text-foreground mb-4">
                    {service.title}
                  </h2>
                  <p className="text-muted-foreground mb-6 leading-relaxed">
                    {service.description}
                  </p>
                  <ul className="space-y-3 mb-8">
                    {service.features.map((feature) => (
                      <li key={feature} className="flex items-center gap-3">
                        <div className="w-5 h-5 rounded-full bg-primary-50 flex items-center justify-center flex-shrink-0">
                          <Check className="w-3 h-3 text-primary" />
                        </div>
                        <span className="text-foreground">{feature}</span>
                      </li>
                    ))}
                  </ul>
                  <Link href="/contact">
                    <Button className="gap-2">
                      Konsultasi Layanan Ini
                      <ArrowRight className="w-4 h-4" />
                    </Button>
                  </Link>
                </div>
                <div className={`${index % 2 === 1 ? "lg:order-1" : ""}`}>
                  <div className="aspect-square rounded-3xl bg-gradient-to-br from-primary-50 to-muted flex items-center justify-center">
                    <service.icon className="w-32 h-32 text-primary/30" />
                  </div>
                </div>
              </motion.div>
            ))}
          </div>
        </div>
      </section>

      {/* Process */}
      <section className="py-24 bg-muted">
        <div className="container mx-auto px-4 lg:px-8">
          <SectionTitle
            badge="Our Process"
            title="Cara Kami Bekerja"
            description="Proses terstruktur untuk memastikan project selesai dengan hasil terbaik."
          />

          <div className="relative">
            {/* Connection line */}
            <div className="hidden lg:block absolute top-1/2 left-0 right-0 h-0.5 bg-border -translate-y-1/2" />

            <div className="grid grid-cols-1 md:grid-cols-3 lg:grid-cols-5 gap-8">
              {process.map((step, index) => (
                <motion.div
                  key={step.title}
                  initial={{ opacity: 0, y: 20 }}
                  whileInView={{ opacity: 1, y: 0 }}
                  viewport={{ once: true }}
                  transition={{ duration: 0.5, delay: index * 0.1 }}
                  className="relative text-center"
                >
                  <div className="w-16 h-16 rounded-2xl bg-primary flex items-center justify-center mx-auto mb-4 relative z-10">
                    <step.icon className="w-8 h-8 text-white" />
                  </div>
                  <h3 className="font-semibold text-foreground mb-2">
                    {step.title}
                  </h3>
                  <p className="text-sm text-muted-foreground">
                    {step.description}
                  </p>
                </motion.div>
              ))}
            </div>
          </div>
        </div>
      </section>

      {/* Pricing */}
      <section className="py-24">
        <div className="container mx-auto px-4 lg:px-8">
          <SectionTitle
            badge="Pricing"
            title="Paket Harga"
            description="Pilih paket yang sesuai dengan kebutuhan dan budget Anda."
          />

          <div className="grid md:grid-cols-3 gap-8">
            {pricing.map((plan, index) => (
              <motion.div
                key={plan.name}
                initial={{ opacity: 0, y: 20 }}
                whileInView={{ opacity: 1, y: 0 }}
                viewport={{ once: true }}
                transition={{ duration: 0.5, delay: index * 0.1 }}
              >
                <div
                  className={`h-full rounded-3xl p-8 ${
                    plan.popular
                      ? "bg-gradient-to-b from-primary to-primary-dark text-white"
                      : "bg-card border border-border"
                  }`}
                >
                  {plan.popular && (
                    <span className="inline-block px-3 py-1 text-xs font-semibold bg-white text-primary rounded-full mb-4">
                      Most Popular
                    </span>
                  )}
                  <h3 className={`text-xl font-bold mb-2 ${plan.popular ? "text-white" : "text-foreground"}`}>
                    {plan.name}
                  </h3>
                  <div className="mb-4">
                    <span className={`text-3xl font-bold ${plan.popular ? "text-white" : "text-foreground"}`}>
                      Rp {plan.price}
                    </span>
                    {plan.price !== "Custom" && (
                      <span className={plan.popular ? "text-white/70" : "text-muted-foreground"}>
                        +
                      </span>
                    )}
                  </div>
                  <p className={`mb-6 ${plan.popular ? "text-white/80" : "text-muted-foreground"}`}>
                    {plan.description}
                  </p>
                  <ul className="space-y-3 mb-8">
                    {plan.features.map((feature) => (
                      <li key={feature} className="flex items-center gap-3">
                        <Check className={`w-5 h-5 ${plan.popular ? "text-white" : "text-primary"}`} />
                        <span className={plan.popular ? "text-white" : "text-foreground"}>
                          {feature}
                        </span>
                      </li>
                    ))}
                  </ul>
                  <Link href="/contact" className="block">
                    <Button
                      className={`w-full ${
                        plan.popular
                          ? "bg-white text-primary hover:bg-white/90"
                          : ""
                      }`}
                      variant={plan.popular ? "primary" : "outline"}
                    >
                      Pilih Paket
                    </Button>
                  </Link>
                </div>
              </motion.div>
            ))}
          </div>
        </div>
      </section>

      {/* CTA */}
      <section className="py-24 bg-muted">
        <div className="container mx-auto px-4 lg:px-8 text-center">
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.5 }}
          >
            <h2 className="text-3xl md:text-4xl font-bold text-foreground mb-4">
              Butuh Solusi Custom?
            </h2>
            <p className="text-muted-foreground mb-8 max-w-xl mx-auto">
              Setiap bisnis unik. Hubungi kami untuk diskusi kebutuhan spesifik Anda.
            </p>
            <Link href="/contact">
              <Button size="lg" className="gap-2">
                Konsultasi Gratis
                <ArrowRight className="w-4 h-4" />
              </Button>
            </Link>
          </motion.div>
        </div>
      </section>
    </>
  );
}
